




































html, body {
  margin: 0;
  padding: 0;
  background-color: #fafafa;
  height: 100%;
  background-image: url("./assets/books.jpg");
  background-size: cover;
}
td, html, body{
  font-family: 'Roboto', 'Helvetica', sans-serif;
}

.logo {
  color: white;
  font-weight: bolder;
  font-size: 40px;
  padding: 20px;
}

div, tr, td {
  box-sizing: border-box;
}




